"use client";
import { create, StateCreator } from "zustand";
import { createJSONStorage, devtools, persist } from "zustand/middleware";
import { IUrlParams } from "../types/url-params";
import { urlParamsSchema } from "../schemas/url-prams.schema";
import { ZodIssue } from "zod";

export interface IUrlParamStore {
  params: IUrlParams | null;
  loading: boolean;
  error?: ZodIssue[];
  loadParams: (params: IUrlParams | null) => void;
}

const localMiddlewares = (f: StateCreator<IUrlParamStore>) =>
  devtools(
    persist(f, {
      name: "url-param",
      storage: createJSONStorage(() => sessionStorage),
    }),
  );

export const useUrlParamStore = create<IUrlParamStore>()(
  localMiddlewares((set) => ({
    params: null,
    loading: true,
    loadParams: (params: IUrlParams | null) => {
      const result = urlParamsSchema.safeParse(params);
      if (result.success) {
        set({ params, loading: false, error: undefined });
      } else {
        set({ params: null, loading: false, error: result.error.issues });
      }
    },
  })),
);
